<template lang="html">



  <div class="usluga">

    <div class="headUsluga">


  <router-link class="navbar-brand" to="/ksusluge">Back </router-link>

    <h3>Outsource HR funkcije</h3>
    <p>Outsource HR funkcije je usluga koja Liderski profiliše našu kompaniju na tržištu Srbije. Podrazumeva kompletan/delimičan outsource svih HR procesa, osim personalne administracije i obračuna zarada. Obuhvata sve oblasti rada u upravljanju ljudskim resursima : Performance Management, Talent Management, Projekte za unapređenje korporativne i organizacione kulture, Selekciju, Razvoj i Edukaciju zaposlenih. Mi postajemo vaš Interni HR Partner.</p>
    <h5>Potrebna Vam je ova usluga? Popunite polja ispod:</h5>
    </div>

    <div class="card-body">

         <!-- @submit.prevent="registerUser"  -->
         <div class="form-group">
           <label for="numberOfEmployees">Broj zaposlenih u kompaniji</label>
           <input
             class="form-control"
             type="text"
             placeholder=""
             name="numberOfEmployees"
             v-model="numberOfEmployees"
             id="numberOfEmployees"
             >
         </div>
        <div class="form-group">
          <label for="companyName">Naziv Kompanije</label>
          <input
            class="form-control"
            type="text"
            placeholder=""
            name="companyName"
            v-model="companyName"
            id="companyName"
            >
        </div>
        <div class="form-group">
          <label for="contactName">Ime i Prezime Kontakt Osobe</label>
          <input
            class="form-control"
            type="text"
            placeholder=""
            name="contactName"
            v-model="contactName"
            id="contactName"
            >
        </div>
        <div class="form-group">
          <label for="contactNumber">Kontakt telefon</label>
          <input
            class="form-control"
            type="text"
            placeholder=""
            name="contactNumber"
            v-model="contactNumber"
            id="contactNumber"
            >
        </div>
        <div class="form-group">
          <label for="email">E-mail</label>
          <input
            class="form-control"
            type="text"
            placeholder=""
            name="email"
            v-model="email"
            id="email"
            >
        </div>




        <button class="btn btn-primary" @Click="submit">Posalji Formular</button>
        &nbsp;&nbsp;&nbsp;&nbsp;



  </div>

  </div>

</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
export default {

  name: 'OutsourceHR',
  components: {

  },
  data(){
    return{
      companyName:"",
  contactName:"",
  contactNumber:"",
  email:"",
  numberOfEmployees :"",
    }
  },
  methods:{
  ...mapActions(['submitServiceAplication']),
  submit(){



    let text = [];
    text.push("Outsource HR funkcije");
    text.push("Naziv Kompanije: "+this.companyName);
    text.push("Ime i Prezime Kontakt Osobe: "+this.contactName);
    text.push("Email: "+this.email);
    text.push("Kontakt telefon: "+this.contactNumber  );
    text.push("Broj zaposlenih u kompaniji: "+this.numberOfEmployees);

    let data ={
      formular:text
    }

    this.submitServiceAplication(data);
  },
},

  beforeCreate: function() {
        document.body.className = 'usluge';
  }
}

</script>

